<template>
  <div class="content-wrapper">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>策略引擎</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="30">
        <el-col :span="5">
          <el-input v-model="query_info.strategy_name" placeholder="策略名称" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.owner" placeholder="创建人" @change="search"></el-input>
        </el-col>
        <el-col :span="2" :push="3">
          <el-button type="primary" @click="clear">清除</el-button>
        </el-col>
        <el-col :span="2" :push="3">
          <el-button type="success" @click="dialogVisible = true">新建</el-button>
        </el-col>
        <el-col :span="2" :push="3">
          <el-button type="danger" @click="remove">删除</el-button>
        </el-col>
        <el-col :span="2" :push="4">
          <el-button type="success" plain @click="ioConfigVisible = true">配置IO</el-button>
        </el-col>
        <el-col :span="2" :push="4">
          <el-button type="success" plain @click="scriptConfigVisible = true">配置脚本</el-button>
        </el-col>
        <el-col :span="2" :push="4">
          <el-button type="success" plain @click="remove">配置故障</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-table :data="strategy_list" @selection-change="handleSelectionChange" border stripe>
          <el-table-column type="selection" width="40"></el-table-column>
          <el-table-column prop="strategy_name" label="策略名称"></el-table-column>
          <el-table-column prop="owner" label="创建人" width="200"></el-table-column>
        </el-table>
      </el-row>
      <el-dialog title="新建策略" :visible.sync="dialogVisible" width="35%">
        <el-card></el-card>
      </el-dialog>
      <IoConfig :pVisible="ioConfigVisible" @changeVisible="closeIoConfig"></IoConfig>
      <ScriptConfig :pVisible="scriptConfigVisible" @changeVisible="closeScriptConfig"></ScriptConfig>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="query_info.page"
        :page-sizes="[30, 50, 100]"
        :page-size="query_info.per_page"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
import IoConfig from '../components/IoConfig.vue'
import ScriptConfig from '../components/ScriptConfig.vue'

export default {
  components: {
    IoConfig,
    ScriptConfig
  },
  data () {
    return {
      query_info: {
        strategy_name: '',
        owner: '',
        page: 1,
        per_page: 30
      },
      new_strategy: {
        strategy_name: '',
        owner: ''
      },
      strategy_list: [],
      selected_strategy_list: [],
      total: 0,
      dialogVisible: false,
      ioConfigVisible: false,
      scriptConfigVisible: false
    }
  },
  created () {
    this.getStrategyList()
  },
  methods: {
    getStrategyList () {
      this.$http.get('strategy', { params: this.query_info })
        .then(response => {
          if (response.data.result !== true) {
            return this.$message({ showClose: true, message: '获取数据失败', type: 'error', duration: 1000 })
          } else {
            this.strategy_list = response.data.data
            this.total = response.data.total
          }
        })
    },
    handleSizeChange (newSize) {
      this.query_info.per_page = newSize
      this.getStrategyList()
    },
    handleCurrentChange (newPage) {
      this.query_info.page = newPage
      this.getStrategyList()
    },
    handleSelectionChange (val) {
      this.selected_strategy_list = val
    },
    search () {
      this.getStrategyList()
    },
    clear () {
      this.query_info.page = 1
      this.query_info.strategy_name = ''
      this.query_info.owner = ''
      this.getStrategyList()
    },
    remove () {
      if (this.selected_strategy_list.length === 0) {
        return this.$message({ message: '请选择需要删除的数据', type: 'error', duration: 1000 })
      }
      for (const strategy of this.selected_strategy_list) {
        const codition = { strategy_name: strategy.strategy_name }
        this.$http.delete('strategy', { params: codition })
          .then(response => {
            if (response.data.result !== true) {
              this.$message({ message: '删除数据失败', type: 'error', duration: 1000 })
            } else {
              this.$message({ message: '删除数据成功', type: 'success', duration: 1000 })
            }
          })
          .finally(() => {
            this.getStrategyList()
          })
      }
    },
    closeIoConfig (val) {
      this.ioConfigVisible = val
    },
    closeScriptConfig (val) {
      this.scriptConfigVisible = val
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  min-height: 670px;
  max-height: 670px;
  overflow: auto;
}
.el-button {
  width: 90px;
}
</style>
