<template>
  <div class="content-wrapper">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>脚本运行数据</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="30">
        <el-col :span="3">
          <el-input v-model="query_info.script_name" placeholder="脚本名称" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.script_path" placeholder="脚本路径" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.script_params" placeholder="脚本参数" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.classification" placeholder="脚本分类" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.script_desc" placeholder="脚本描述" @change="search"></el-input>
        </el-col>
        <el-col :span="2">
          <el-input v-model="query_info.device_model" placeholder="设备类型" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.device_version" placeholder="设备版本" @change="search"></el-input>
        </el-col>
        <el-col :span="2">
          <el-input v-model="query_info.date" placeholder="运行日期" @change="search"></el-input>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="clear">清除</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-table :data="his_script_list" @selection-change="handleSelectionChange" border stripe>
          <el-table-column type="selection" width="40"></el-table-column>
          <el-table-column prop="script_name" label="脚本名称" width="140"></el-table-column>
          <el-table-column prop="script_path" label="脚本路径" width="140"></el-table-column>
          <el-table-column prop="script_params" label="脚本参数"></el-table-column>
          <el-table-column prop="classification" label="脚本分类"></el-table-column>
          <el-table-column prop="script_desc" label="脚本描述"></el-table-column>
          <el-table-column prop="device_model" label="设备类型"></el-table-column>
          <el-table-column prop="device_version" label="设备版本"></el-table-column>
          <el-table-column prop="date" label="运行日期"></el-table-column>
        </el-table>
      </el-row>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="query_info.page"
        :page-sizes="[30, 50, 100]"
        :page-size="query_info.per_page"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      query_info: {
        script_name: '',
        script_path: '',
        script_params: '',
        classification: '',
        script_desc: '',
        device_model: '',
        device_version: '',
        date: '',
        page: 1,
        per_page: 30
      },
      his_script_list: [],
      selected_his_list: [],
      total: 0
    }
  },
  created () {
    this.getHisScriptList()
  },
  methods: {
    getHisScriptList () {
      this.$http.get('script/his_script', { params: this.query_info })
        .then(response => {
          if (response.data.result !== true) {
            return this.$message({ showClose: true, message: '获取数据失败', type: 'error', duration: 1000 })
          } else {
            this.his_script_list = response.data.data
            this.total = response.data.total
          }
        })
    },
    handleSizeChange (newSize) {
      this.query_info.per_page = newSize
      this.getHisScriptList()
    },
    handleCurrentChange (newPage) {
      this.query_info.page = newPage
      this.getHisScriptList()
    },
    handleSelectionChange (val) {
      this.selected_his_list = val
    },
    search () {
      this.getHisScriptList()
    },
    clear () {
      this.query_info.page = 1
      this.query_info.script_name = ''
      this.query_info.script_path = ''
      this.query_info.script_params = ''
      this.query_info.classification = ''
      this.query_info.script_desc = ''
      this.query_info.device_model = ''
      this.query_info.device_version = ''
      this.query_info.date = ''
      this.getHisScriptList()
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  min-height: 670px;
  max-height: 670px;
  overflow: auto;
}

.el-button {
  width: 90px;
}
</style>
