<template>
  <div>
    <el-dialog :visible="pVisible" :before-close="closeDialog"></el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    pVisible: Boolean
  },
  data () {
    return {
      visible: this.pVisible
    }
  },
  watch: {
    pVisible (val) {
      this.visible = val
    },
    visible (val) {
      this.$emit('changeVisible', val)
    }
  },
  methods: {
    closeDialog () {
      this.visible = false
    }
  }

}
</script>

