<template>
  <div class="content-wrapper">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>IO运行数据</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="30">
        <el-col :span="3">
          <el-input v-model="query_info.io_name" placeholder="IO名称" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-select v-model="query_info.tool_name" placeholder="IO工具" @change="search">
            <el-option :key="item" :lable="item" :value="item" v-for="item in tools_list"></el-option>
          </el-select>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.io_params" placeholder="IO参数" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.device_model" placeholder="设备型号" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.device_version" placeholder="设备版本" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.script_name" placeholder="脚本名称" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.date" placeholder="日期" @change="search"></el-input>
        </el-col>
        <el-col :span="2" :push="1">
          <el-button type="primary" @click="clear">清除</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-table :data="his_io_list" @selection-change="handleSelectionChange" border stripe>
          <el-table-column type="selection" width="40"></el-table-column>
          <el-table-column prop="io_name" label="IO名称" width="200"></el-table-column>
          <el-table-column prop="tool_name" label="IO工具" width="200"></el-table-column>
          <el-table-column prop="io_params" label="IO参数"></el-table-column>
          <el-table-column prop="device_model" label="设备模型"></el-table-column>
          <el-table-column prop="device_version" label="设备版本"></el-table-column>
          <el-table-column prop="script_name" label="脚本名称"></el-table-column>
          <el-table-column prop="date" label="日期"></el-table-column>
        </el-table>
      </el-row>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="query_info.page"
        :page-sizes="[30, 50, 100]"
        :page-size="query_info.per_page"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      query_info: {
        io_name: '',
        tool_name: '',
        io_params: '',
        device_model: '',
        device_version: '',
        script_name: '',
        date: '',
        page: 1,
        per_page: 30
      },
      tools_list: ['vdbench', 'uniblkIO', 'unifsIO'],
      his_io_list: [],
      selected_io_list: [],
      total: 0
    }
  },
  created () {
    this.getHisIoList()
  },
  methods: {
    getHisIoList () {
      this.$http.get('/io/his_io', { params: this.query_info })
        .then(response => {
          if (response.data.result !== true) {
            return this.$message({ showClose: true, message: '获取IO模型库失败', type: 'error', duration: 1000 })
          } else {
            this.his_io_list = response.data.data
            this.total = response.data.total
          }
        })
    },
    handleSizeChange (newSize) {
      this.query_info.per_page = newSize
      this.getHisIoList()
    },
    handleCurrentChange (newPage) {
      this.query_info.page = newPage
      this.getHisIoList()
    },
    handleSelectionChange (val) {
      this.selected_io_list = val
    },
    search () {
      this.getHisIoList()
    },
    clear () {
      this.query_info.page = 1
      this.query_info.io_name = ''
      this.query_info.tool_name = ''
      this.query_info.io_params = ''
      this.query_info.device_model = ''
      this.query_info.device_version = ''
      this.query_info.script_name = ''
      this.query_info.date = ''
      this.getHisIoList()
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  min-height: 670px;
  max-height: 670px;
  overflow: auto;
}

.el-button {
  width: 90px;
}
</style>
