<template>
  <div class="content-wrapper">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>脚本数据库</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="30">
        <el-col :span="3">
          <el-input v-model="query_info.script_name" placeholder="脚本名称" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.classification" placeholder="脚本分类" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.script_params" placeholder="脚本参数" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.script_path" placeholder="脚本路径" @change="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="query_info.script_desc" placeholder="脚本描述" @change="search"></el-input>
        </el-col>
        <el-col :span="2" :push="3">
          <el-button type="primary" @click="clear">清除</el-button>
        </el-col>
        <el-col :span="2" :push="3">
          <el-button type="success">新建</el-button>
        </el-col>
        <el-col :span="2" :push="3">
          <el-button type="danger" @click="remove">删除</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-table :data="script_list" @selection-change="handleSelectionChange" border stripe>
          <el-table-column type="selection" width="40"></el-table-column>
          <el-table-column prop="script_name" label="脚本名称" width="300"></el-table-column>
          <el-table-column prop="classification" label="脚本分类" width="200"></el-table-column>
          <el-table-column prop="script_params" label="脚本参数" width="300"></el-table-column>
          <el-table-column prop="script_path" label="脚本路径"></el-table-column>
          <el-table-column prop="script_desc" label="脚本描述" width="200"></el-table-column>
        </el-table>
      </el-row>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="query_info.page"
        :page-sizes="[30, 50, 100]"
        :page-size="query_info.per_page"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      query_info: {
        script_name: '',
        script_path: '',
        script_params: '',
        classification: '',
        script_desc: '',
        page: 1,
        per_page: 30
      },
      script_list: [],
      selected_script_list: [],
      total: 0
    }
  },
  created () {
    this.getScriptList()
  },
  methods: {
    getScriptList () {
      this.$http.get('script', { params: this.query_info })
        .then(response => {
          if (response.data.result !== true) {
            return this.$message({ showClose: true, message: '获取数据失败', type: 'error', duration: 1000 })
          } else {
            this.script_list = response.data.data
            this.total = response.data.total
          }
        })
    },
    handleSizeChange (newSize) {
      this.query_info.per_page = newSize
      this.getScriptList()
    },
    handleCurrentChange (newPage) {
      this.query_info.page = newPage
      this.getScriptList()
    },
    handleSelectionChange (val) {
      this.selected_script_list = val
    },
    search () {
      this.getScriptList()
    },
    clear () {
      this.query_info.page = 1
      this.query_info.io_name = ''
      this.query_info.script_name = ''
      this.query_info.script_path = ''
      this.query_info.script_params = ''
      this.query_info.classification = ''
      this.query_info.script_desc = ''
      this.getScriptList()
    },
    remove () {
      if (this.selected_script_list.length === 0) {
        return this.$message({ message: '请选择需要删除de数据', type: 'error', duration: 1000 })
      }
      for (const script of this.selected_script_list) {
        const codition = { script_name: script.script_name }
        this.$http.delete('script', { params: codition })
          .then(response => {
            if (response.data.result !== true) {
              this.$message({ message: '删除数据失败', type: 'error', duration: 1000 })
            } else {
              this.$message({ message: '删除数据成功', type: 'success', duration: 1000 })
            }
          })
          .finally(() => {
            this.getScriptList()
          })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  min-height: 670px;
  max-height: 670px;
  overflow: auto;
}

.el-button {
  width: 90px;
}
</style>
